.button {
  box-sizing: border-box;
  padding: 5px 24px;
  cursor: pointer;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  border-radius: 3px;
  border: none;
}

.disabled {
  background-color: #004D99;
  opacity: 0.5;
}

.secondary {
  color: #0066CC;
  background: #fff;
  border: 1px solid #0066CC;
}

.default {
  color: #fff;
  background-color: #0066CC;
}

.red {
  color: #fff;
  background-color: #CC1439;
}

.default:hover {
  background-color: #0080FF;
}

.red:hover {
  background-color: #FF1A47;
}

.secondary:hover {
  background-color: #E5F0FA;
}


.default:active {
  background-color: #0059B3;
}

.secondary:active {
  background-color: #C9E5FF;
}

.red:active {
  background-color: #B31232;
}

.button:disabled {
  background-color: #004D99;
  opacity: 0.5;
}
